html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: $body-font-family;
  background-color: $basic-white;
  overflow-x: hidden;
}

body .modal__window {
  width: auto;
}

body .modal-show {
  height: 100vh;
  overflow-y: scroll;
}

.fixed {
  position: fixed;
  overflow-y: hidden;
}
